:root {
    --primary: #1888ff;

}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    margin-right: 1.2rem;
    outline: none;
    border: none;
    font-size: 18px;
    color: #fff;
    cursor: pointer;
    background-color: var(--primary);
}

.btn:hover {
    padding: 6px 18px;
    transition: all 0.3s ease-out;
    background-color:transparent;
    color: #fff;
    border-radius: 4px;
    border: 2px solid var(--primary)
}