
  .box {
  margin-left: 16%;
   margin-top: 7%;
   
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  width: 16%;
}
.card-text{
  font-size: 0.9rem;
  padding: 0.4rem;
  
}
.container-fluid{
  
  padding-top: 6rem;
}

.overflow{
  
  overflow: hidden;
}


.box:hover {
  
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  transform: scale(1.8);
  transform: scale(1);
  transition: transform 0.5 ease;
}

  .grids {
    max-width: 10%;
    margin: auto;
  }  
 
