body {
     /* background-image: url('./images/pocetna.jpg'); */
    padding: 0px;
    margin: 0;
  }
  h1, h2, p, ul, li {
    font-family: sans-serif;
  }
  ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
  }
  .naslov {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    text-align: center;
    
    color: white;
      
      
  }
  ul.header {
   background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);;
    padding: 0;
  }
  ul.header li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
  }
  .content {
    background-color: #FFF;
    padding: 20px;
  }
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
  }


  .triler {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
  }
  .container{
    padding-top: 40%;
    margin-left: 60%;
    background-color: blanchedalmond;
  }
  
  

  .box-container {
    margin: 10px 0;
    display: none;
  }
  
  .box {
    width: 150px;
    
    margin-left:85% ;
    padding: 7px;
    line-height: 25px;
    float: left;
    border-radius: 4px;
    border: 1px solid #999;
  }
  
  .clearboth {
    clear: both;
  }







  .boxx {
    margin-left: 18%;
     margin-top: 7%;
     
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 16%;
  }
  .card-text{
    font-size: 0.9rem;
    padding: 0.4rem;
    
  }
  .container-fluid{
    
    padding-top: 6rem;
  }
  
  .overflow{
    
    overflow: hidden;
  }
  
  
  .boxx:hover {
    
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    transform: scale(1.8);
    transform: scale(1);
    transition: transform 0.5 ease;
  }
  
    .grids {
      max-width: 10%;
      margin: auto;
    } 


  

   


    